*{
  margin:0 ;
  padding: 0;
}
body {
  background: #fff;
  color: black;
  font-weight: 300;
}

.wrapper {
  width: 1280px;
  margin:  50px auto;
}

header{
  position: relative;
}

header .logo {
  font-weight: 600;
  font-size: 20px;
}

header ul.nav{
  float: right;
  list-style: none;
}

header ul.nav li{
 display: inline;
 margin-left: 25px;
 cursor: pointer;
 transition: opacity 500ms ease;
}

header ul.nav li:hover{
  opacity: 0,5;
}

header .shop-cart-button{
  float: right;
  cursor: pointer;
  transition: color 500ms ease;
}

header .shop-cart-button:hover,
header .shop-cart-button.active {
  color: #dc3d3d;
}

header .shop-cart{
  position: absolute;
  top: 30px;
  right: 0;
  width: 450px;
  z-index: 1000;
  padding: 20px;
  background-color: gray;
  padding-bottom: 0;
}

header .shop-cart .empty h2{
  font-size: 20px;
  margin-bottom: 20px;
}

header .shop-cart .item{
  width: 100%;
  float: left;
  margin-bottom: 20px;
}

header .shop-cart .item img{
  width: 70px;
  float: left;
  margin-right: 20px;
}

header .shop-cart .item img h2{
  font-size: 20px;
  margin-bottom: 10px;
}

header .shop-cart .item img p{
  color: #797979;
  font-weight: 600;
}

header .shop-cart .item .delete-icon{
  color: #ca5252;
  float: right;
  position: relative;
  top: -25px;
  cursor:pointer;
  transition: color, transform 500ms ease;
}

header .shop-cart .item .delete-icon:hover{
 color:#d83030 ;
 transform: scale(1.5);
}

header .shop-cart .summa{
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
}


header .presentation{
  margin: 50px 0;
  background: url('./img/back.jpeg');
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color:#bcbcbc ;
  position: relative;
}

header .presentation::after{
content: 'AppleHome';
position: absolute;
top:100px;
left: 50px;
width: 300px;
font-size: 40px;
font-weight: 600;
color: #fff;
}

header .presentation::before{
  
  position: absolute;
  top:150px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: #fff;
  }

main{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}

main .item{
  width: 30%;
  margin-bottom: 50px;
  background: #f5f5f5 ;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;
}

main .item img{
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 500ms ease;
}

main .item img:hover{
  transform: scale(1.05);
}

main h2,
main p,
 main b {
 margin: 10px 20px;
 color: #333;
}

main b{
  color: #5fa36a;
}

main  .add-to-cart {
position: absolute;
right: 20px ;
bottom: 20px;
background: #ca5252;
width: 35px;
height: 35px;
line-height: 35px;
text-align: center;
color: #fff;
border-radius: 50%;
cursor: pointer;
font-weight: 600;
transition: transform 500ms ease;

}

main  .add-to-cart:hover{
  transform: scale(1.5) translateY(-5px);
}

  footer{
    text-align: center;
    margin-top: 100px;
  }